import Presentation from "augmentedjs-next-presentation";
import Table from "./table.js";

class Router extends Presentation.Router {
  constructor() {
    super({
      "routes": {
        "table": "table"
      }
    });
  };
  table() {
    this.loadView(Table)
  };
};

export default Router;



